import React, {useState} from "react"

import 'swiper/css';
import 'swiper/css/pagination'
import 'swiper/css/autoplay'
import letter from './../asset/letter_tr.png'


const Form = () => {

    const [wr_name, setName] = useState("");
    const [number, setNumber] = useState("");
    const [disabled, setDisabled] = useState(false);

    const handleChange = (event) => { 
        setName(event.target.value); 
    }

    const numberChange = (event) => { 
        setNumber(event.target.value); 
    }

    const agree2 = document.querySelector("#agree2")
    const agree1 = document.querySelector("#agree1")


    const handleSubmit = async (event) =>{
        setDisabled(true);
        event.preventDefault();
        if(!agree1.checked||!agree2.checked){
            setDisabled(false);
        }
            else{
                await new Promise((r)=>setTimeout(r,1000));
                alert(`${wr_name}  .  ID(${number})  .  .`)
                setDisabled(false);}}

    const agree = () => {
        if (!document.getElementById('agree1').checked) {
          alert('  ');
        }
        if (!document.getElementById('agree2').checked) {
          alert('   .');
        }}

    return(
        <section id="form-all" className='container-lg p-4'>
            <h2 id="form-title" className="text-center text-white pt-5 pb-4">
                ,<span className="md-block"></span>  ! 
                <img id="e-title" src={letter} className="img-fluid" alt=" "/> </h2>
            <div id="form-date" className="text-center text-white pb-4">2023 <span>5 27 ~ 28</span>(~)<span className="sm-block"></span>  7~12 <span className="sm-block md-block"></span>(6  500M  ) </div>
            <form action="" id="form-box" className="d-lg-flex border-radius" onSubmit={handleSubmit}>
                <div id="img"></div>
                <div id="form-group" className="m-auto">
                    <div className="sm-wrap">
                        <label htmlFor="name" className="nametag me-10"></label>
                        <input type="text" name="name" placeholder="" value={wr_name} onChange={handleChange} />
                    </div>
                    <div className="sm-wrap d-flex">
                        <label id="gendername" htmlFor="sex" className="nametag me-10"></label><span className="sm-block"></span>
                        <input type="radio" name="gender" value="woman" id="woman"/><label htmlFor="woman"><span className="womantag me-4">&nbsp;</span> </label>
                        <input type="radio" name="gender" value="man" id="man"/><label htmlFor="man">&nbsp;<span></span></label> 
                    </div>
                    <div className="sm-wrap">
                        <label htmlFor="age" className="nametag me-10">&nbsp;<span className="text-danger s-middle">*</span></label>
                        <select name="age" id="age-group" required>
                            <option value="null">----</option>
                            <option value="10" >10</option>
                            <option value="20">20</option>
                            <option value="30">30</option>
                            <option value="40">40</option>
                            <option value="50">50 </option>
                        </select>
                    </div>
                    <div className="sm-wrap">
                        <label htmlFor="location" className="nametag me-10"></label>
                        <select defaultValue="null" name="location" id="age-group">
                            <option value="null">----</option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                        </select>
                    </div>
                    <div className="sm-wrap">
                        <label htmlFor="number" className="nametag me-10">&nbsp;<span className="text-danger s-middle">*</span></label>
                        <input type="text" name="number" className="mb-2" placeholder=" ID " onChange={numberChange} required/>
                    </div>
                    <ul id="label2" className="mx-0">
                            <li className="mx-0 mb-3">
                                <input type="checkbox" id="agree1"/>
                                <label htmlFor="agree1" className="d-md-flex align-items-center">&nbsp;<span>  .</span>
                                <a href="#" className="d-flex justify-content-center">&nbsp;<span className="sm-block"></span><p className="agree-btn">[]</p></a></label>
                            </li>
                            <li className="mx-0 mb-5">
                                <input type="checkbox" id="agree2"/>
                                <label htmlFor="agree2" className="d-md-flex align-items-center">&nbsp;<span>Recozy    .</span><a href="#" className="d-flex justify-content-center">&nbsp;<span className="sm-block"></span><p className="agree-btn">[]</p></a></label>
                            </li>
                    </ul>
                    <button id="sm-btn" className="d-block mx-auto mb-4" type="submit" onClick={agree} disabled={disabled} ></button>
                </div>
            </form>
            <div id='textinfo' className='text-white d-lg-flex flex-wrap justify-content-center'>
                <div>*       10   .</div>
                <div>*         .</div>
                <div>*  1 1 ,    4   .</div>
                <div>*     5 .</div>
            </div>
        </section>
    )
}

export default Form
